* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
}

.ContainerCard {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  /* border: 3px solid #000; */
  border-radius: 2rem;
  width: 30rem;
  padding: 20px;
  background-color: white;
  overflow: hidden;
  position: relative;

  /* margin-bottom: 40px; */
  top: 1rem;
  position: relative;
  box-shadow: 10px 10px 20px #babecc, -10px -10px 20px #ffffff;
  box-sizing: content-box;
  transition: all 0.3s ease;
}

.ContainerCard::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.2;
  z-index: 0;
  transition: opacity 0.3s ease;
}

.ContainerCard:hover::before {
  opacity: 0.4;
}

.ContainerCard img,
.ContainerCard span {
  position: relative;
  z-index: 1;
}

.ContainerCard:hover {
  transform: translateY(-5px);
  box-shadow: 10px 10px 20px #a9cccb, -10px -10px 20px #ffffff;
}

img {
  width: 8rem;
  /* filter: drop-shadow(1px 1px 20px rgb(93, 235, 207)); */
  height: 114px;
  margin-right: 20px;
}

.ContainerCard h3 {
  font-size: 18px;
  color: #000;
}

/* Gradientes espec√≠ficos para cada tarjeta */
.ContainerCard:nth-child(1)::before {
  background: linear-gradient(135deg, #4B8BBE, #FFE873);
}

.ContainerCard:nth-child(2)::before {
  background: linear-gradient(135deg, #5382A1, #F89820);
}

.ContainerCard:nth-child(3)::before {
  background: linear-gradient(135deg, #61DAFB, #282C34);
}

.ContainerCard:nth-child(4)::before {
  background: linear-gradient(135deg, #00A5DE, #FFFFFF);
}